* {
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: linear-gradient(to left, rgba(255, 0, 0, 0.247), blue);
  height: 100vh;
}

.appWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.title {
  text-align: center;
  background: linear-gradient(to left, rgba(255, 0, 0, 0.247), blue);
  color: white;
  padding-top: 3rem;
}

.newsFeed {
  background-color: rgba(173, 250, 224, 0.486);
  padding: 10px;
  margin: 5px;
  box-shadow: 0 3px 10px rgb(0, 0, 0.2);
  max-width: 300px;
  height: 50vh;
}

.newsFeed a {
  text-decoration: none;
  color: blueviolet;
}

.currencyConverter {
  background-color: rgb(230, 223, 211);
  padding: 10px;
  margin: 5px;
  box-shadow: 0 3px 10px rgb(0, 0, 0.2);
  text-align: center;
  height: 50vh;
}

.exchangeRate {
  background-color: rgb(221, 230, 221);
  padding: 20px;
  margin-top: 100px;
  box-shadow: 0 3px 10px rgb(0, 0, 0.2);
}

input,
select {
  border: none;
  padding-bottom: 1rem;
}

button {
  border: none;
  float: right;
  margin: 2px;
  font-family: sans-serif;
  padding: 1rem;
  border-radius: 10px;
  transition: 0.3s;
}

button:hover {
  background-color: rgba(255, 127, 80, 0.712);
  transition: 0.3s;
}
